import { Component, EventEmitter, Input, Output } from '@angular/core';
import { CockpitConfig, CockpitConfigRootNode } from './cockpit-config.model';

@Component({
  selector: 'c8y-root-node-config',
  templateUrl: './root-node-config.component.html'
})
export class RootNodeConfigComponent {
  @Input()
  config: CockpitConfig;

  @Output()
  onUpdate = new EventEmitter<void>();

  /**
   * Removes one of the root nodes.
   * @param node The node to remove.
   */
   removeNavigatorNode(node: CockpitConfigRootNode) {
    const index = this.config.rootNodes.indexOf(node);
    if (index > -1) {
      this.config.rootNodes.splice(index, 1);
      this.onUpdate.emit();
    }
  }
}
