import { Inject, Injectable, Optional } from '@angular/core';
import { InventoryService } from '@c8y/client';
import {
  AppStateService,
  NavigatorNode,
  NavigatorService,
  OptionsService,
  Permissions,
  SearchService,
  Tab,
  TabsService
} from '@c8y/ngx-components';
import {
  AssetNavigatorConfig,
  AssetNodeService,
  ASSET_NAVIGATOR_CONFIG
} from '@c8y/ngx-components/assets-navigator';
import { isUndefined, startCase } from 'lodash-es';
import { map } from 'rxjs/operators';
import { CockpitConfig, DEFAULT_CONFIG, DEFAULT_HOME_DASHBOARD_NAME } from './cockpit-config.model';

@Injectable({
  providedIn: 'root'
})
export class CockpitConfigService {
  currentConfig: CockpitConfig = DEFAULT_CONFIG;
  nodes: NavigatorNode[] = [];

  private readonly DEFAULT_NODE_PRIORITY = 2000;

  get excludedFeatureKeys() {
    return Object.keys(this.currentConfig.features).filter(
      key => !this.currentConfig.features[key]
    );
  }

  constructor(
    private navigatorService: NavigatorService,
    private tabsService: TabsService,
    private searchService: SearchService,
    private assetNodeService: AssetNodeService,
    private inventoryService: InventoryService,
    private appState: AppStateService,
    private optionsService: OptionsService,
    private permissions: Permissions,
    @Optional() @Inject(ASSET_NAVIGATOR_CONFIG) public moduleConfig: AssetNavigatorConfig
  ) {
    this.registerFilterForFeatures();
    this.init();
  }

  init() {
    this.appState.currentApplicationConfig.subscribe(config => {
      if (config) {
        this.currentConfig = { ...DEFAULT_CONFIG, ...config } as CockpitConfig;
        this.setRootNodes();
      }
    });
  }

  async saveConfig(config) {
    this.currentConfig = config;
    await this.storeApplicationConfig(this.currentConfig);
  }

  refresh() {
    this.optionsService.hideNavigator = this.currentConfig.hideNavigator;
    this.navigatorService.refresh();
    this.searchService.refresh();
  }

  async setRootNodes() {
    this.addNodesToFactories();
    for (const node of this.currentConfig.rootNodes) {
      const { data } = await this.inventoryService.detail(node.id);
      if (data) {
        this.nodes.push(
          this.assetNodeService.createAssetNode({
            mo: data,
            hideDevices: node.hideDevices,
            priority: isUndefined(this.moduleConfig?.rootNodePriority)
              ? this.DEFAULT_NODE_PRIORITY
              : this.moduleConfig.rootNodePriority
          })
        );
      }
    }
    this.refresh();
  }

  getAppDashboardName() {
    return `${DEFAULT_HOME_DASHBOARD_NAME.substring(0, DEFAULT_HOME_DASHBOARD_NAME.length - 1)}_${
      this.appState.state.app.id
    }`;
  }

  private async storeApplicationConfig(config: CockpitConfig) {
    await this.appState.updateApplicationConfig<CockpitConfig>(config);
  }

  private addNodesToFactories() {
    const nodeInFactories = this.navigatorService.factories.find(
      nodes => nodes === this.nodes
    ) as NavigatorNode[];
    const isNodeAlreadyInFactories = !!nodeInFactories;
    if (isNodeAlreadyInFactories) {
      this.nodes = nodeInFactories;
      this.nodes.length = 0;
    } else {
      this.navigatorService.factories.push(this.nodes);
    }
  }

  private registerFilterForFeatures() {
    this.navigatorService.items$ = this.navigatorService.items$.pipe(
      map(nodes => this.setHiddenAttrLock(nodes)),
      map(nodes => this.filterNavigatorNode(nodes))
    );
    this.tabsService.items$ = this.tabsService.items$.pipe(map(tabs => this.filterTabs(tabs)));
    this.searchService.items$ = this.searchService.items$.pipe(
      map(search => (this.currentConfig.features.search ? search : []))
    );
  }

  private setHiddenAttrLock(nodes) {
    nodes.forEach(node => {
      Object.keys(this.currentConfig.features).forEach(key => {
        const childNode = node.find(startCase(key).toLowerCase());

        if (childNode) {
          if (
            !this.permissions.hasRole('ROLE_APPLICATION_MANAGEMENT_ADMIN') &&
            childNode.lockHiddenAttr === undefined &&
            childNode.hidden === true
          ) {
            childNode.lockHiddenAttr = childNode.hidden;
          }
        }
      });
    });
    return nodes;
  }

  private filterTabs(tabs: Tab[]) {
    return tabs.filter(
      tab => !this.excludedFeatureKeys.some(key => tab.featureId === key)
    );
  }

  private filterNavigatorNode(nodes: NavigatorNode[]) {
    if (!this.currentConfig) {
      return nodes;
    }
    const disabledFeatures = this.excludedFeatureKeys;
    const filteredNodes = nodes.filter(
      node => !disabledFeatures.some(key => node.featureId === key)
    );

    this.showAllChildrenNodes(nodes);
    this.hideChildrenNodesThatAreDisabled(nodes, disabledFeatures);
    return filteredNodes;
  }

  private hideChildrenNodesThatAreDisabled(nodes: NavigatorNode[], disabledFeatures: string[]) {
    nodes.forEach(node =>
      disabledFeatures.forEach(key => {
        const childNode = node.find(key, 'featureId');
        if (childNode) {
          childNode.hidden = true;
        }
      })
    );
  }

  private showAllChildrenNodes(nodes: NavigatorNode[]) {
    nodes.forEach(node => {
      Object.keys(this.currentConfig.features).forEach(key => {
        const childNode = node.find(startCase(key).toLowerCase());

        if (childNode) {
          if (childNode.lockHiddenAttr === true) {
            return;
          }

          childNode.hidden = false;
        }
      });
    });
  }
}
