import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import {
  AppStateService,
  gettext,
  NavigatorNode,
  NavigatorNodeFactory,
  Permissions
} from '@c8y/ngx-components';
import { EcosystemService } from '@c8y/ngx-components/ecosystem';
import { COCKPIT_CONFIG_PATH } from './cockpit-config.model';

@Injectable({
  providedIn: 'root'
})
export class CockpitConfigGuard implements CanActivate, NavigatorNodeFactory {
  protected readonly configNode: NavigatorNode = new NavigatorNode({
    path: `/${COCKPIT_CONFIG_PATH}`,
    parent: gettext('Configuration'),
    label: gettext('App configuration'),
    icon: 'imac-settings',
    preventDuplicates: true
  });

  constructor(
    private permissions: Permissions,
    private appState: AppStateService,
    private ecosystemService: EcosystemService
  ) {}

  get(): NavigatorNode {
    if (this.canActivate()) {
      return this.configNode;
    }
    return;
  }

  canActivate(): boolean {
    return (
      this.permissions.hasRole('ROLE_APPLICATION_MANAGEMENT_ADMIN') &&
      this.ecosystemService.isOwner(this.appState.currentApplication.value)
    );
  }
}
